@charset "UTF-8";

@import "base";


.inner{margin: 0 auto; max-width: 1660px; padding: 0 40px; width: 100%; height: 100%;}

/* form style */
.input_box{display: flex; align-items: center;}
.input_box > p{margin-right: 12px;}
.input_box .w120{width: 120px;}
.input_box .w160{width: 160px;}

.input_style_1{padding: 10px 15px; height: 40px; color: #fff; font-size: 18px; border-radius: 20px; background-color: rgba(255,255,255,0.2);}
.input_style_2{height: 46px; font-size: 15px; color: #222; border-bottom: 1px solid #c8b38e;}
.input_style_2::placeholder{font-size: 15px; color: #b79b6d;}

.select_style{padding: 0 10px; width: 100%; height: 46px; font-size: 15px; color: #b79b6d; background: #fff url("../img/icon/ico_down.png") no-repeat center right 10px; background-size: 22px auto; border: 1px solid #bfa57a; appearance: none; -webkit-appearance: none; /* 사파리, 크롬 하위버전용 */ -moz-appearance: none; /* 사파리, 크롬 하위버전용 */}

.submit_btn_1{width: 190px; height: 40px; font-size: 22px; font-weight: 700; background-color: #d9b67d; border-radius: 20px; box-shadow: 3px 4px 5px 0px rgba(0, 0, 0, 0.2);}

/* header */
.header{z-index: 100; position: fixed; left: 0; top: 0; width: 100%; height: 80px; background-color: rgba(0,0,0,0.3);}
.header .inner{position: relative;}
.header .inner .alert_msg{z-index: -1; opacity: 0; display: flex; align-items: center; padding: 0 3px; position: absolute; right: 0; bottom: 0; width: 224px; height: 34px; font-size: 18px; color: #fff; border-radius: 20px; background-color: rgba(0,0,0,0.3); transition: opacity 0.2s;}
.header .inner .alert_msg::before{content:""; display: block; margin-right: 14px; width: 28px; height: 28px; background: url("../img/icon/ico_alert.png") no-repeat center center; background-size: 100% auto;}
.header .inner .alert_msg.show{z-index: 1; opacity: 1; bottom: -50px;}
.header .apply_wrap{display: flex; align-items: center; justify-content: flex-end; height: 100%; color: #fff;}
.header .apply_wrap .input_box{margin-right: 30px;}

/* footer */
.float_btn{z-index: 100; position: fixed; right: 100px; bottom: 100px;}
.float_btn a{display: block;}

.footer{padding: 80px 0 70px; background-color: #222222;}
.footer .inner{position: relative;}
.footer .logo{margin-bottom: 26px; width: 223px;}
.footer address{display: flex; align-items: center; flex-wrap: wrap;}
.footer address p{position: relative; margin-right: 16px; margin-bottom: 14px; padding-right: 16px; font-size: 15px; color: #bfbfbf; font-weight: 300;}
.footer address p::after{content:""; position: absolute; right: 0; top: 0; width: 1px; height: 15px; background-color: #bfbfbf;}
.footer address p:last-of-type{padding-right: 0; margin-right: 0;}
.footer address p:last-of-type::after{display: none;}
.footer address p a{text-decoration: underline; font-weight: 700;}
.footer .link_list{display: flex; align-items: center; position: absolute; right: 0; bottom: 0;}
.footer .link_list li{margin-right: 48px;}
.footer .link_list li:last-of-type{margin-right: 0;}

/* popup */
.popup{z-index: 9999; position: fixed; left: 50%; top: 50%; transform: translate(-50%, -50%); padding: 0 40px; max-width: 830px; width: 100%;}
.popup .pop_inner{overflow: hidden; border-radius: 10px;}
.popup .bottom_btn{display: flex; justify-content: space-between; align-items: center; padding: 0 15px; width: 100%; height: 56px; background-color: #000;}
.popup .bottom_btn .today_close{}
.popup .bottom_btn .today_close label{font-size: 20px; color: #fff;}
.popup .bottom_btn .today_close label::after{content:""; display: inline-block; margin-left: 8px; width: 18px; height: 18px; background: url("../img/icon/ico_check_off.png") no-repeat center center; background-size: 100% auto; vertical-align: middle;}
.popup .bottom_btn .today_close input[type='checkbox']{display: none;}
.popup .bottom_btn .today_close input[type='checkbox']:checked + label::after{background-image: url("../img/icon/ico_check_on.png");}
.popup .bottom_btn .close_btn{width: 28px;}
.popup.close_pop{display: none;}

/* contents */
.main_sec{overflow: hidden; position: relative; margin: 0 auto; max-width: 1920px; width: 100%; height: 100%;}
.main_sec .swiper-slide{position: relative;}
.main_sec .swiper-slide-active{z-index: 1;}
.main_sec .main-scrollbar{left: 50%; bottom: 100px; transform: translateX(-50%); width: calc(100% - 80px); max-width: 800px; background-color: rgba(255,255,255,0.2);}
.main_sec .swiper-scrollbar-drag{background-color: rgba(255,255,255,0.6);}
.main_sec .inner{position: absolute; left: 50%; top: 0; width: 100%; height: 100%; transform: translateX(-50%);}
.main_sec .txt_con{position: absolute; left: 40px; top: 50%; transform: translateY(-50%);}
.main_sec .img_con{display: block; width: 100%;}

.tit_con{}
.tit_con h2{margin-bottom: 10px; font-size: 30px; font-family: 'NanumMyeongjo'; font-weight: 700; color: #d9b67d;}
.tit_con p{font-size: 32px; line-height: 1.3;}
.tit_con span{font-weight: 900;}
.tit_con span.sm{font-size: 18px; color: #767676; font-weight: 400;}
.tit_con .sub_tit{margin-top: 40px; font-size: 18px; color: #767676;}

.news_sec{padding: 80px 0;}
.news_sec .tit_con{padding-top: 20px;}
.news_sec .inner{display: flex; justify-content: space-between;}
.news_sec .news_list{display: flex; align-items: stretch; width: 65.2%;}
.news_sec .news_list .news_card{max-width: 500px; width: calc(50% - 15px); background-color: #f5f5f5; border-radius: 10px;}
.news_sec .news_list .news_card:last-of-type{margin-left: 30px;}
.news_sec .news_list .news_card a{display: block; position: relative; width: 100%; height: 100%; padding: 35px 35px 70px 35px;}
.news_sec .news_list .news_card a::after{content:""; position: absolute; top: 38px; right: 40px; width: 24px; height: 24px; background: url("../img/icon/ico_plus.png") no-repeat center center; background-size: 100% auto;}
.news_sec .news_list .news_card h3{overflow: hidden; margin-bottom: 20px; width: 90%; min-height: 65px; font-size: 25px; font-weight: 900; line-height: 1.3; text-overflow: ellipsis; display: -webkit-box; -webkit-box-orient: vertical; -webkit-line-clamp: 2;}
.news_sec .news_list .news_card .desc{overflow: hidden; width: 90%; margin-bottom: 30px; font-size: 18px; color: #767676; text-overflow: ellipsis; white-space: nowrap;}
.news_sec .news_list .news_card .date{position: absolute; left: 35px; bottom: 35px; width: 90%; font-size: 18px; color: #767676;}

.map_sec{padding: 135px 0; border-top: 6px solid #b79b6d; background-color: #f1f2f2;}
.map_sec .inner{position: relative;}
.map_sec .tit_con{position: absolute; left: 40px; top: 0;}
.map_sec .tit_con h2{margin-bottom: 25px;}
.map_sec .map_con{display: flex; flex-direction: row-reverse; align-items: flex-end; justify-content: space-between;}
.map_sec .map_con .img_con{min-width: 700px; width: 56.8%;}
.map_sec .map_con .list_con{display: flex; padding: 10px 0; flex-wrap: wrap; margin:0 10px; width: 625px; border-top: 2px solid #bea67e; border-bottom: 2px solid #bea67e; counter-reset: list;}
.map_sec .map_con .list_con li{padding: 10px 0 10px 5px; font-size: 22px; font-weight: 700; color: #b79b6d; letter-spacing: -0.05rem;}
.map_sec .map_con .list_con li::before{display: inline-block; counter-increment: list; content:counter(list); margin-right: 8px; width: 20px; height: 20px; text-align: center; line-height: 20px; font-size: 18px; color: #fff; border-radius: 50%; background-color: #b79b6d; vertical-align: middle;}
.map_sec .map_con .list_con li:nth-of-type(2n-1){width: 54%;}
.map_sec .map_con .list_con li:nth-of-type(2n){width: 46%;}

.about_sec .banner{position: absolute; transform: translateX(-50%);}
.about_sec .banner::before{content:""; position: absolute; left: -4px; bottom: -6px; width: calc(100% + 4px); height: calc(100% + 6px); background: linear-gradient(-45deg, transparent 12px, rgba(0,0,0,0.2) 0);}
.about_sec .banner span{display: block; position: relative; padding: 5px 20px 10px 10px; font-size: 23px; color: #fff; background: linear-gradient(-45deg, transparent 12px, #b79b6d 0); white-space: nowrap;}
.about_sec .txt_con h3{font-size: 30px; font-weight: 700; color: #d9b67d; font-family: 'NanumMyeongjo';}
.about_sec .txt_con .desc{font-size: 18px; color: #fff;}
.about_sec .txt_con .tag{margin-bottom: 5px; font-size: 23px; font-weight: 300; color: #fff;}
.about_sec .txt_con .tit{font-size: 26px; color: #fff;}

.about_sec .swiper-pagination{display: flex; justify-content: center; left: 50%; bottom: 60px; transform: translateX(-50%);}
.about_sec .swiper-pagination .swiper-pagination-bullet{opacity: 0.3; width: 8px; height: 8px; margin: 0 15px; background-color: #fff;}
.about_sec .swiper-pagination .swiper-pagination-bullet-active{opacity: 0.7;}

.service_sec{background-color: #394a53; border-top: 6px solid #b79b6d;}
.service_sec .banner{left: 65.5%;}
.service_sec .service_con{overflow: hidden; position: relative; padding: 80px 0 120px;}
.service_sec .service_con .swiper-slide{display: flex; flex-direction: row-reverse; justify-content: space-between; align-items: center;}
.service_sec .service_con .swiper-slide .img_con{width: 49.3%;}
.service_sec .service_con .swiper-slide .txt_con{margin-left: 12.65%; width: 38%;}
.service_sec .service_con .swiper-slide .txt_con h3{margin-bottom: 30px;}
.service_sec .service_con .swiper-slide .txt_con .box{display: inline-block; margin-top: 70px; padding: 10px 20px; font-size: 20px; line-height: 1.3; color: #fff; background-color: #232d32;}
.service_sec .service_con .swiper-slide .txt_con .box span{display: block; font-weight: 900;}
 
.resort_sec{border-top: 6px solid #b79b6d; background: url("../img/resort_bg_01.jpg") no-repeat center center; background-size: cover;}
.resort_sec .banner{left: 31.4%;}
.resort_sec .resort_con{overflow: hidden; position: relative; padding: 90px 0 110px;}
.resort_sec .resort_con .swiper-slide{display: flex; align-items: center;}
.resort_sec .resort_con .swiper-slide .img_con{width: 46.6%;}
.resort_sec .resort_con .swiper-slide .txt_con{margin-right: 7.3%; width: 46.1%; text-align: right;}
.resort_sec .resort_con .swiper-slide .txt_con h3{margin-bottom: 50px;}
.resort_sec .resort_con .swiper-slide .txt_con .tit{margin-bottom: 25px;}

.premium_sec{border-top: 6px solid #b79b6d; background: url("../img/premium_bg_01.jpg") no-repeat center center; background-size: cover;}
.premium_sec .banner{left: 68.5%;}
.premium_sec .premium_con{overflow: hidden; position: relative; padding: 60px 0 120px;}
.premium_sec .premium_con .swiper-wrapper{align-items: center;}
.premium_sec .premium_con .swiper-slide{display: flex; flex-direction: row-reverse; justify-content: space-between; align-items: center;}
.premium_sec .premium_con .swiper-slide .img_con{width: 49.6%;}
.premium_sec .premium_con .swiper-slide .txt_con{width: 50.4%;}
.premium_sec .premium_con .swiper-slide .txt_con h3{margin-bottom: 40px;}

.premium_sec .premium_con .swiper-slide .txt_con{margin-top: 50px; margin-left: 12.67%;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con{display: flex; margin-top: 30px;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con dl{display: flex; flex-direction: column-reverse; align-items: center; justify-content: flex-start;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con dl + dl {margin-left: 40px;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con dt{margin-top: 20px; font-size: 18px; color: #fff; text-align: center;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con dd{width: 169px;}

.premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type2{margin: 106px 0 0 -70px;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type2 dl{margin-bottom: auto;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type2 dl + dl {margin-left: 20px;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type2 dd{width: 123px;}

.premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type3{margin: 106px 0 0 -70px;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type3 dl{margin-bottom: auto;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type3 dl + dl {margin-left: 30px;}
.premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type3 dd{width: 145px;}

.type_sec{padding: 90px 0 130px; border-top: 3px solid #efefef;}
.type_sec .tit_con{margin-bottom: 60px;}
.type_sec .type_list{overflow: hidden; position: relative; margin: 0 auto; max-width: 1920px;}
.type_sec .type_list .swiper-slide{overflow: hidden; position: relative; width: 480px; border-radius: 20px;}
.type_sec .type_list .swiper-slide .txt_con{position: absolute; left: 0; top: 0; width: 100%; height: 62px; font-size: 25px; line-height: 62px; font-weight: 900; color: #fff; text-align: center; background-color: rgba(0,0,0,0.5);}
.type_sec .type_list .type-button{z-index: 10; display: flex; justify-content: space-between; position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); padding: 0 40px; max-width: 1660px; width: 100%;}
.type_sec .type_list .swiper-button-prev,
.type_sec .type_list .swiper-button-next{position: relative; margin-top: 0; width: 82px; height: 82px;}
.type_sec .type_list .swiper-button-prev{background: url("../img/icon/ico_prev.png") no-repeat center center; background-size: 100% auto;}
.type_sec .type_list .swiper-button-next{background: url("../img/icon/ico_next.png") no-repeat center center; background-size: 100% auto;}
.type_sec .type_view{margin-top: 70px;}
.type_sec .type_view h3{margin-bottom: 20px; font-size: 30px; color: #d9b67d; font-family: 'NanumMyeongjo'; text-align: center;}
.type_sec .type_view .vr_con{position: relative; margin: 0 auto; max-width: 1280px; width: 100%;}
.type_sec .type_view .vr_con::before{content:""; display: block; padding-top: 53.2%; width: 100%;}
.type_sec .type_view .vr_con iframe{position: absolute; left: 0; top: 0; width: 100%; height: 100%;}
.type_sec .type_view .vr_con a{z-index: 10; position: absolute; right: 28px; top: 25px; width: 210px; height: 64px; line-height: 64px; text-align: center; color: #fff; font-size: 25px; font-weight: 700; background-color: #ffa813; border-radius: 33px;}
.type_sec .type_view .txt_con{margin: 20px auto 0; max-width: 1280px; width: 100%;}
.type_sec .type_view .txt_con dt{margin-bottom: 8px; font-size: 25px; font-weight: 900;color: #404040;}
.type_sec .type_view .txt_con dd{font-size: 18px; color: #767676;}

.jeju_sec{padding: 100px 0 60px; background-color: #efefef;}
.jeju_sec .inner{position: relative;}
.jeju_sec .tit_con{position: absolute; left: 40px; top: 0;}
.jeju_sec .jeju_map{display: flex;}
.jeju_sec .jeju_map .outline{margin-top: 330px; margin-right: -10.5%; flex:0 0 53.2%;}
.jeju_sec .jeju_map .picture{flex:0 0 57%;}
.jeju_sec .jeju_map .picture ul{display: flex; justify-content: center; margin: -80px auto 0;}
.jeju_sec .jeju_map .picture ul li{position: relative; width: 173px; font-size: 18px; color: #b79b6d; text-align: center; line-height: 1.2;}
.jeju_sec .jeju_map .picture ul li p{position: absolute; left: 50%; bottom: -65px; transform: translateX(-50%); white-space: nowrap;}
.jeju_sec .jeju_map .picture ul li span{display: block; font-weight: 900;}
.jeju_sec .jeju_map .picture ul li + li{margin-left: 40px;}

.contact_sec{padding: 140px 0 170px; background-color: #eae4d1;}
.contact_sec .inner{max-width: 1200px;}
.contact_sec h2{margin-bottom: 65px; font-size: 30px; font-weight: 900;}
.contact_sec .contact_form{}
.contact_sec .contact_form dl{}
.contact_sec .contact_form dl dt{margin-bottom: 10px;}
.contact_sec .contact_form dl dd{width: 100%;}
.contact_sec .contact_form dl dd input{width: 100%;}
.contact_sec .contact_form .input_con{display: flex; margin-bottom: 55px;}
.contact_sec .contact_form .input_con dl{margin-right: 54px; width: calc(33.33% - 36px);}
.contact_sec .contact_form .input_con dl:last-of-type{margin-right: 0;}
.contact_sec .contact_form .select_con{display: flex; margin-bottom: 80px;}
.contact_sec .contact_form .select_con dl{margin-right: 54px; width: calc(66.66% - 18px);}
.contact_sec .contact_form .select_con dl:last-of-type{margin-right: 0; width: calc(33.33% - 36px);}
.contact_sec .contact_form .submit_con{display: flex; align-items: flex-end; justify-content: space-between;}
.contact_sec .contact_form .submit_con .check_list li{margin-bottom: 16px;}
.contact_sec .contact_form .submit_con .check_list li:last-of-type{margin-bottom: 0;}
.contact_sec .contact_form .submit_con .check_list li input[type='checkbox']{display: none;}
.contact_sec .contact_form .submit_con .check_list li label{font-size: 16px;}
.contact_sec .contact_form .submit_con .check_list li label a{text-decoration: underline;}
.contact_sec .contact_form .submit_con .check_list li label .box{display: inline-block; position: relative; margin-right: 12px; width: 14px; height: 14px; border: 1px solid #a4a198; vertical-align: middle;}
.contact_sec .contact_form .submit_con .check_list li input:checked + label .box{border-color: #222;}
.contact_sec .contact_form .submit_con .check_list li input:checked + label .box::after{content:""; position: absolute; left: 60%; top: 30%; transform: translate(-50%, -50%); width: 21px; height: 13px; background: url("../img/icon/ico_check.png") no-repeat center center; background-size: 100% auto;}
.contact_sec .contact_form .submit_con .submit_btn{width: 580px; height: 66px; font-size: 25px; font-weight: 700; color: #fff; background-color: #b79b6d; border-radius: 33px;}

/* notice */
.notice_wrap{display: none; z-index: 500; overflow-y: auto; position: fixed; left: 0; top: 0; padding: 40px; width: 100%; height: 100vh; background-color: rgba(0,0,0,0.5);}
.notice_wrap.show{display: block;}
.notice_wrap .notice_frame{display: flex; align-items: center; justify-content: center; height: fit-content;}
.notice_wrap .notice_inner{padding: 40px; max-width: 1080px; width: 100%; min-height: 775px; background-color: #fff; border-radius: 10px;}
.notice_wrap .tit_con{display: flex; justify-content: space-between; align-items: flex-start;}
.notice_wrap .tit_con h4{overflow: hidden; width: calc(100% - 40px); font-size: 32px; text-overflow: ellipsis; display: -webkit-box; -webkit-box-orient: vertical; -webkit-line-clamp: 2;}
.notice_wrap .tit_con h4 span{font-weight: 700;}
.notice_wrap .tit_con .close_btn{margin-top: 10px; width: 28px;}
.notice_wrap .list_con{margin-top: 30px;}
.notice_wrap .list_con .lists li a{display: block; padding: 25px 0; border-bottom: 1px solid #eee;}
.notice_wrap .list_con .lists li a dl{display: flex; align-items: center; justify-content: space-between;}
.notice_wrap .list_con .lists li a dt{width: calc(100% - 100px);}
.notice_wrap .list_con .lists li a dt strong{display: block; overflow: hidden; margin-bottom: 10px; white-space: nowrap; text-overflow: ellipsis; font-size: 20px; font-weight: 700;}
.notice_wrap .list_con .lists li a dt span{display: block; overflow: hidden; white-space: nowrap; text-overflow: ellipsis; font-size: 16px; color: #767676;}
.notice_wrap .list_con .lists li a dd{width: 100px;}
.notice_wrap .list_con .paging{display: flex; margin-top: 30px; align-items: center; justify-content: center;}
.notice_wrap .list_con .paging li{margin: 0 5px;}
.notice_wrap .list_con .paging li a{display: block; width: 24px;  height: 24px; text-align: center; line-height: 24px; color: #999999;}
.notice_wrap .list_con .paging li.active a{background: #d9b67d; border-radius: 50%; color: #fff;}
.notice_wrap .detail_con{margin-top: 35px; border-top: 2px solid #000;}
.notice_wrap .detail_con .desc{padding: 35px 0; min-height: 530px; font-size: 18px; color: #767676; border-bottom: 1px solid #dddddd;}
.notice_wrap .detail_con .list_btn{display: block; margin: 30px auto 0; width: 480px; height: 54px; line-height: 54px; font-size: 20px; font-weight: 700; text-align: center; background-color: #b79b6d; color: #fff; border-radius: 27px;}

.notice_wrap .notice_list{display: none;}
.notice_wrap .detail{display: none;}

@media all and (max-width:1660px){
    .float_btn{right: 40px;}
}

@media all and (max-width:1500px){
    .map_sec .map_con .list_con li{font-size: 20px;}
}

@media all and (max-width:1400px){
    .main_sec .txt_con{width: 300px;}
    .main_sec .img_con{position: relative; left: 50%; width: auto; height: 800px; transform: translateX(-50%);}

    .map_sec .map_con .list_con li{font-size: 18px; letter-spacing: -0.15rem;}

    .premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type2 dd{width: 100px;}
    .premium_sec .premium_con .swiper-slide .txt_con .thumb_con.type3 dd{width: 130px;}

    .type_sec .type_list .swiper-slide{width: 360px;}
    .type_sec .type_list .swiper-slide .txt_con{font-size: 22px;}
}

@media all and (max-width:1300px){
    .map_sec .map_con .img_con{min-width: 600px;}
    .map_sec .map_con .list_con li{font-size: 16px;}
}

@media all and (max-width:1200px){
    .map_sec{padding: 100px 0; border-top: 6px solid #b79b6d;}
    .map_sec .tit_con{position: relative; left: 0; margin-bottom: 50px;}
    .map_sec .tit_con h2{margin-bottom: 25px;}
    .map_sec .map_con{display: block;}
    .map_sec .map_con .img_con{margin: 0 auto; min-width: auto; max-width: 800px; width: 100%;}
    .map_sec .map_con .list_con{margin-top: 40px; padding: 15px 0; margin:40px auto 0; max-width: 800px; width: 100%; border-top: 2px solid #bea67e; border-bottom: 2px solid #bea67e;}
    .map_sec .map_con .list_con li{padding: 10px 0 10px 5px; font-size: 22px; letter-spacing: -0.05rem;}
